<template>
  <div class="emp-sort">
    <div>
      <img class="img-responsive" src="../assets/empSortTop.png">
    </div>
    <div class="search-wrapper">
      <div class="search-content" @click="queryEmpByName">
        <img src="../assets/search.png">
        <!--<input type="search" placeholder="直接搜索选手姓名进行投票" @click="queryEmpByName" v-model="search">-->
        <span class="search-text">直接搜索选手姓名进行投票</span>
      </div>
    </div>
    <mt-navbar v-model="selected">
      <mt-tab-item @click.native="wzPageNumber=0;empTicketWZList=[];getEmpList();" id="1">王者赛区</mt-tab-item>
      <mt-tab-item @click.native="qtPageNumber=0;empTicketXXList=[];getYoungStar();" id="2">新秀赛区</mt-tab-item>
    </mt-navbar>
    <!-- tab-container -->
    <mt-tab-container v-model="selected">
      <mt-tab-container-item id="1">
        <div>
          <EmpSortList :list="empTicketWZList" v-on:voteEvent="userVoteEmpTicket"></EmpSortList>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <div>
          <EmpSortList :list="empTicketXXList" v-on:voteEvent="userVoteEmpTicket"></EmpSortList>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
    <mt-popup v-model="votePopupVisible" popup-transition="popup-fade" closeOnClickModal="false">
      <div class="prompt-form">
        <img class="close" src="../assets/close.png" @click="closePopup">
        <div class="top">
          <img src="../assets/voteHand.png" />
        </div>
        <div class="content">
          <span class="red" @click="reduce">-</span>
          <input type="number" v-model="myVoteTicket" placeholder="请输入投票数量">
          <span class="red" @click="add">+</span>
        </div>
        <div class="tip">
          当前剩余可投票数: {{ticketNumber}}
        </div>
        <div class="btn-wrapper">
          <span class="btn-vote" @click="userVoteEmpBtn">
            确认投票
          </span>
        </div>
      </div>
    </mt-popup>
    <div v-if="more" class="lcj-more" @click="loadBottom">
      <span>查看更多</span>
    </div>
    <div v-if="allLoaded" class="no-more">
      <span>已加载完</span>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import EmpSortList from '@/components/EmpSortList.vue'
import { getEmpList } from '@/api/employee'
import { getTicketNumber, userVoteEmp, getUserTicketSort } from '@/api/ticket'
import { Navbar, TabItem, Toast, MessageBox, Indicator, Loadmore, Popup } from 'mint-ui';
import jsAPI from '../libs/jsAPI'

export default {
  name: 'EmpSort',
  components: {
    EmpSortList
  },
  data() {
    return {
      search: '',
      empEntity: {},
      empTicketWZList: [],
      empTicketXXList: [],
      selected: "1",
      wzPageNumber: 0,
      qtPageNumber: 0,
      allLoaded: false,
      votePopupVisible: false,
      myVoteTicket: 0,
      more: false,
      allLoaded: false
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.user;
    }
  },
  created() {
    let titleObject = {
      back: true,
      romoveMarginTop: false,
      title: "服务之星排行榜"
    }
    this.$emit("routerChange", titleObject);
    //this.getTicketNumber();
    this.getEmpList();
  },
  computed: {
    userInfo() {
      return this.$store.state.user;
    },
    ticketNumber() {
      return this.$store.state.ticketNumber
    }
  },
  methods: {
    add: function () {
      if (this.myVoteTicket == '' || this.myVoteTicket == ' ') {
        this.myVoteTicket = 0;
      }
      this.myVoteTicket = parseInt(this.myVoteTicket);
      this.myVoteTicket += 1;
    },
    reduce: function () {
      if (this.myVoteTicket <= 0) {
        return;
      }
      this.myVoteTicket = parseInt(this.myVoteTicket);
      this.myVoteTicket -= 1;
    },
    userVoteEmpBtn() {
      console.log('userVoteEmpBtn');
      console.log(this.empEntity);
      if (this.myVoteTicket < 1) {
        Toast({
          message: '1票起投',
          position: 'middle',
          duration: 2000
        });
        return;
      }
      if (this.myVoteTicket > this.ticketNumber) {
        Toast({
          message: '超过最大票数',
          position: 'middle',
          duration: 2000
        });
        return;
      }
      this.userVoteEmp(this.empEntity.empId, this.myVoteTicket);
      this.votePopupVisible = false;
    },
    queryEmpByName() {
      console.log(this.search);
      this.$router.push('/search')
    },
    getTicketNumber() {
      getTicketNumber().then(res => {
        //this.ticketNumber = res.data.result.number;
        this.$store.commit({
          type: 'setTicketNumber',
          ticketNumber: res.data.result.number
        });
      });
    },
    getEmpList() {
      let params = {
        pageNumber: this.wzPageNumber,
        pageSize: 10,
        divison: 1
      }
      this.allLoaded = false;
      this.more = false;
      Indicator.open({
        text: '加载中...',
        spinnerType: 'triple-bounce'
      });
      getEmpList(params).then(res => {
        Indicator.close();
        if (res.data.resCode == 0) {
          //成功请求数据
          console.log(res);
          this.empTicketWZList = this.empTicketWZList.concat(res.data.result);
          if (this.empTicketWZList.length >= res.data.pagination.totalItems) {
            this.allLoaded = true;
            this.more = false;
          } else {
            this.allLoaded = false;
            this.more = true;
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 3000
          });
        }
      });
    },
    getYoungStar() {
      console.log('getYoungStar');
      let params = {
        pageNumber: this.qtPageNumber,
        pageSize: 10,
        divison: 0
      }
      this.allLoaded = false;
      this.more = false;
      Indicator.open({
        text: '加载中...',
        spinnerType: 'triple-bounce'
      });
      getEmpList(params).then(res => {
        Indicator.close();
        if (res.data.resCode == 0) {
          //成功请求数据
          console.log(res);
          this.empTicketXXList = this.empTicketXXList.concat(res.data.result);
          if (this.empTicketXXList.length >= res.data.pagination.totalItems) {
            this.allLoaded = true;
            this.more = false;
          } else {
            this.allLoaded = false;
            this.more = true;
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 3000
          });
        }
      });
    },
    hasLogin: function () {
      if (this.userInfo.clientId.length > 1) {
        return true;
      }
      return false;
    },
    userVoteEmp: function (empId, ticketNumber) {
      let params = {
        empId,
        ticketNumber
      }
      userVoteEmp(params).then(res => {
        if (res.data.resCode == 0) {
          //成功请求数据
          this.getTicketNumber();
          if (this.selected == '1') {
            this.wzPageNumber = 0;
            this.empTicketWZList = [];
            this.getEmpList();
          } else {
            this.qtPageNumber = 0;
            this.empTicketXXList = []
            this.getYoungStar();
          }
          Toast({
            message: '投票成功',
            position: 'middle',
            duration: 3000
          });
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 3000
          });
        }
      });
    },
    userVoteEmpTicket(data, callback) {
      console.log(data);
      let that = this;
      if (this.hasLogin()) {
        this.empEntity = data.empEntity;
        this.votePopupVisible = true;
      } else {
        this.forwardLogin();
      }
    },
    forwardLogin: function () {
      let that = this;
      window.loginCallBack = function () {
        // 登陆回来从新初始化信息
        console.log('loginCallBack');
        that.$store.dispatch('userLogin');
      }
      jsAPI.forwardLogin('loginCallBack');
    },
    loadBottom() {
      console.log("loadBottom");
      if (this.selected == "1") {
        console.log('selected: ' + this.selected);
        this.wzPageNumber += 1;
        this.getEmpList();
      } else {
        console.log('selected: ' + this.selected);
        this.qtPageNumber += 1;
        this.getYoungStar();
      }
    },
    closePopup: function () {
      this.votePopupVisible = false;
    }
  }
}
</script>
