        outline: none;
        background: none;
        border: 0;
      }
      .search-text {
        color: #dadada;
        font-size: 1.3rem;
      }
    }
  }
  .mint-popup {
    top: 45%;
    width: 100%;
  }
  .prompt-form {
    background: #ffffff;
    margin: 0 13%;
