      font-weight: 500;
      input {
        font-size: 1.8rem;
        outline: none;
        border: none;
        margin: 0 1rem 0 1rem;
        padding: 0.2rem 0;
        width: 50%;
        text-align: center;
      }
      .red {
        color: red;
        font-size: 3.6rem;
        font-weight: 400;
      }
    }
    .tip {
      color: #626262;
